.WebDev3Container{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 9.6%;
    background-color: #3F8FB6;
    display: flex;
    flex-direction: column;
    gap: 10%;
    align-items: center;
    color: white;
    justify-content: center;
}

.stackTechWeb{
    display: flex;
    gap: 20px;
}

.stackTechWeb div{
    display: flex;
    background: linear-gradient(0deg, rgba(44, 42, 42, 0.22), rgba(44, 42, 42, 0.22));
    border: 1px solid rgba(44, 42, 42, 0.34);
    border-radius: 10px;
    align-items: center;
    height: 200px;
    width: 150px;
    align-items: center;
    justify-content: center;
}

.stackTechWeb div img{
    height: 180px;
    width: 130px;
}

.shortLogoAppYard{
    color: white;
    display: flex;
    align-items: center;
    font-size: 60px;
    font-weight: 1000;
    gap: 10px;
}

.nodeImage{
    height: 200px !important;
    width: 250px !important;
}