body{
    background-color: #121212 !important;
}

.LandingPage {
    position: fixed;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #446E83;
    color: white;
    overflow: hidden;
    transition: background-color;
  }
  
  .LandingPage.animation-start {
    background-color: #121212; 
  }
  
  .we {
    font-size: 50px;
    position: absolute;
    opacity: 0;
    transition: opacity 2s ease-in-out;
    background-color: #446E83;
    z-index: 999;

  }
  
  .we-animation1 {
    animation: fadeIn 2s ease-in-out;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  .are-animation1 {
    animation: fadeOut 1s ease-in-out;
  }
  
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  .withyou-animation1 {
    animation: fadeIn 1s ease-in-out;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  .shape-animation {
    animation: changeShape 2s ease-in-out;
  }
  
  @keyframes changeShape {
    0% {
      border-radius: 0%;
      height: 100vh;
      width: 100vw;
    }
    100% {
      border-radius: 50%;
      height: 60vh;
      width: 30vw;
    }
  }
  
  .leftDiv {
    position: absolute;
    top: 18%;
    left: 35%;
    height: 60%;
    width: 30%;
    border-radius: 50%;
    background-color: #446E83;
    opacity: 0;
    animation-delay: 4s;
    animation: moveLeft 5s ease-in-out;
    animation-fill-mode: forwards;
  }
  
  @keyframes moveLeft {
    0% {
      transform: translateX(0);
      opacity: 1;
    }

    30%{
      transform: translate(20%, 10%);

      opacity: 1;
    }
    50% {
        transform: translate(15%, 10%);
        opacity: 1;
    }   

    80%{
      transform: translate(140%, 10%);
      opacity: 1;
    }

    90%{
      transform: translate(130%, 10%);
      opacity: 1;
    }

    100% {
      transform: translate(200%, 10%);
      opacity: 0;
    }
  }
  
  .rightDiv {
    position: absolute;
    top: 18%;
    right: 35%;
    height: 60%;
    width: 30%;
    border-radius: 50%;
    background-color: #446E83;
    opacity: 0;
    animation-delay: 4s;
    animation: moveRight 5s ease-in-out ;
    animation-fill-mode: forwards;
  }
  
  @keyframes moveRight {
    0% {
      transform: translateX(0);
      opacity: 1;
    }

    30%{
      transform: translate(-20%, 10%);

      opacity: 1;
    }
    50% {
        transform: translate(-15%, 10%);
        opacity: 1;
    }   

    80%{
      transform: translate(-140%, 10%);
      opacity: 1;
    }

    90%{
      transform: translate(-130%, 10%);
      opacity: 1;
    }

    100% {
      transform: translate(-200%, 10%);
      opacity: 0;
    }
  }
  
  .circle1{
    height: 300px;
    width: 300px;
    border-radius: 50%;
    border: 10px solid #446E83;
    background-color: #121212;
    left: 43%;
    opacity: 1;
    z-index: 9999;
    position: absolute;
    top: 37%;
